.card{
  border:none;

  &--active{
    background-size: auto 110% !important;

    .card-legend{
      transform: translateY(0);

      p{
        opacity: 1;
      }
    }
  }

  //For referencement
  img{
    visibility: hidden;
  }
}

.card-deck{
  position: relative;
  z-index: 1;
  padding: 0 1.5rem;

  @include mq($from: sm) {
    top: -11rem;
  }
}

.card-picture{
  position: relative;
  margin-bottom: -.2rem;
  background-size: auto 100%;
  background-repeat: no-repeat;
  background-position: center;
  min-height: 22rem;
  border-top: .1rem solid $white;
  border-left: .1rem solid $white;
  border-right: .1rem solid $white;
  cursor: pointer;
  transition: all .5s;
}

.card-legend{
  color: $white;
  position: absolute;
  z-index: 100;
  bottom: 1.5rem;
  left: 1.5rem;
  transform: translateY(2rem);
  transition: all .5s;

  h2{
    @include font($font:$font, $size:2rem, $line_height:2rem, $color:null, $font_weight:$bold);
    text-transform: uppercase;
  }

  p{
    @include font($font:$font, $size:1.6rem, $line_height:2rem, $color:null, $font_weight:$regular);
    opacity: 0;
    transition: opacity .5s;
    transition-delay: .2s;
  }
}

.card-block{
  border: none;
  border-left: .1rem solid $white;
  border-right: .1rem solid $white;
  padding: 0;
  background: $black;
}

.card-collapse{
  border: none;
  background: $black;
}

.card-subcategory{
  li{
    @include font($font:$font, $size:1.8rem, $line_height:2.2rem, $color:$white, $font_weight:$semi-bold);
    text-transform: uppercase;
    position: relative;
    overflow: hidden;

    &:hover{
      a{
        color: $white !important;
      }

      &:before{
        transform: translateX(0%);
      }
    }

    &:before{
      content: '';
      background: rgba($white, .2);
      display: block;
      height: 100%;
      width: 100%;
      position: absolute;
      top: 0;
      left: 0;
      transform: translateX(-100%);
      transition: transform .3s ease-in;
    }

    a{
      letter-spacing: .1rem;
      padding: 1rem 1.5rem;
      margin-left: 1.5rem;
      display: block;
      position: relative;
      z-index: 2;

      &:focus{
        color: inherit;
        text-decoration: none;
      }

      &:before{
        content: "\f0da";
        display: inline-block;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        font: normal normal normal 15px/1 FontAwesome;
        font-size: inherit;
        text-rendering: auto;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
      }
    }
  }
}

.card-paragraph{
  @include font($font:$font, $size:1.4rem, $line_height:2rem, $color:null, $font_weight:$regular);
}

.card-footer{
  border: none;
  background: none;
}

.card-animation{
  > div{
    opacity: 0;
    transform: translateY(-2rem);
    transition: opacity .3s ease-in, transform .8s ease-out;
  }

  &--welcome{
    > div{
      &:first-child{
        opacity: 1;
        transform: translateY(0);
        transition-delay: .2s;
      }

      &:nth-child(2){
        opacity: 1;
        transform: translateY(0);
        transition-delay: .4s;
      }

      &:last-child{
        opacity: 1;
        transform: translateY(0);
        transition-delay: .6s;
      }
    }
  }
}