.filterExpanded{
  background: linear-gradient(to bottom, rgba(4,62,123,1) 0%,rgba(4,62,123,1) 100%);

  &_wrapper{
    padding: 2rem;

    li:last-child{
      padding-top: 2rem;
      margin-top: 2rem;
      border-top: solid .1rem rgba($white, .3);
    }
  }

  &_title{
    @include font($font:$font, $size:1.4rem, $line_height:1.4rem, $color:$white, $font_weight:$bold);
    text-transform: uppercase;
  }
}

.filter{
  margin-top: -5.5rem;

  &_sticky{
    padding-top: 5.5rem;
    background-color: $background-gray;
  }

  &--fixed{
    position: fixed;
    margin-top: 0;
    z-index: 50;
    top: 0;
    left: 0;
    right: 0;

    .toolBar{
      box-shadow: -12px 5px 186px -2px rgba(224,224,224,1);
    }
  }

  &_seeAll{
    display: inline-block;
  }

  &_wrapper{
    position: relative;
    display: inline-block;
    width: 85%;
  }

  &_group{
    margin-bottom: 2rem;
    width: 80%;
    margin: auto;
    overflow-x: hidden;

    @include mq($from: md) {
      width: 90%;
    }

    &_state{
      border-radius: 50%;
      width: 1.5rem;
      height: 1.5rem;
      text-align: center;
      border: solid .1rem black;
      display: inline-block;
    }

    &_button{
      background-color: $white;
      display: inline-block;
      padding: .8rem 1.2rem;
      margin-right: 1rem;
      border-radius: .5rem;
      text-transform: uppercase;
      border: none;
      @include font($font:$font, $size:1.3rem, $line_height:1.4rem, $color:$gray, $font_weight:$semi-bold);

      &--isChecked{
        background-color: $black;
        color: $white;
      }
    }

    //Slider
    .swiper-slide{
      width: auto;
    }

    .swiper-button-prev--machine,
    .swiper-button-next--machine,
    .swiper-button-prev--application,
    .swiper-button-next--application{
      cursor: pointer;
      text-align: center;
      line-height: 4rem;
      position: absolute;
      width: 4rem;
      height: 4rem;
      top: 50%;
      color: $white;
      font-size: 2rem;
      background: none;
      margin-top: 0;
      transform: translateY(-50%);
    }

    .swiper-button-prev--machine,
    .swiper-button-prev--application{
      left: -1rem;
    }

    .swiper-button-next--machine,
    .swiper-button-next--application{
      right: -1rem;
    }
  }
}
