.home{
  &_slider{
    margin-bottom: 2rem;
    overflow: hidden;

    @include mq($from: sm) {
      overflow-x: hidden;
      margin-bottom: 0;
    }

    &_item{
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;    
      padding: 3rem 0;
      height: 40vh;

      @include mq($from: sm) {
        padding: 0;
        height: 60vh;
      }
    }

    &_baseline{
      @include mq($from: md) {
        margin-top: 10vh;
        margin-left: 10%;
      }

      h3{
        @include font($font:$font, $size:3.5rem, $line_height:4.5rem, $color:$white, $font_weight:$bold);
        margin-bottom: 1rem;

        @include mq($from: sm) {
          @include font($font:$font, $size:4.5rem, $line_height:6rem, $color:$white, $font_weight:$bold);
        }
      }
      span{
        @include font($font:$font, $size:2rem, $line_height:2.5rem, $color:$white, $font_weight:$regular);
        display: block;
      }
    }

    &_button{
      color: $white;
      font-family: $font-title;
      font-weight: $bold;
      text-transform: uppercase;
      background: linear-gradient(to bottom, #248ebb 0%,#1a698a 100%);
      font-size: 1.5rem;
      padding: 1rem 1rem;
      margin-top: 3rem;
      letter-spacing: .1rem;
      display: inline-block;

      @include mq($from: md) {
        font-size: 1.8rem;
        padding: 1rem 1.5rem;
      }

      &:hover{
          color: $white !important;
      }

      i{
        padding-left: .5rem;
      }
    }

    .swiper-button-prev,
    .swiper-button-next{
      display: none;

      @include mq($from: md) {
        color: $white;
        font-size: 2rem;
        width: 4.5rem;
        height: 4.55rem;
        background: none;
        background-color: rgba(0,0,0, .4);
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 0;
        transform: translateY(-50%);
      }
    }

    .swiper-button-prev{
      left: 10vw;
    }

    .swiper-button-next{
      right: 10vw;
    }
  }
}