.toolBar{
  background: white;

  &_title{
    @include font($font:$font, $size:1.6rem, $line_height:1.8rem, $color:$gray, $font_weight:$bold);
    text-transform: uppercase;

    &--category{
      span{
        padding-right: 0;
        border-right: .1rem solid rgba($gray, .0);
        transition: all 1s;
      }

      i{
        content: "\f107";
        cursor: pointer;
        display: inline-block;
        @include font($font:FontAwesome, $size:2.8rem, $line_height:2.8rem, $color:transparent, $font_weight:bold);
        text-rendering: auto;
        padding: 1rem 0 1.5rem 1rem;
        margin-left: -.5rem;
        position: relative;
        top: .2rem;
        transform: translateX(100%);
        visibility: hidden;
        transition: all 1s;
      }
    }

    &--active{
      span{
        padding-right: 1rem;
        border-right: .1rem solid rgba($gray, .3);
      }

      i{
        color: $gray;
        transform: translateX(0%);
        visibility: visible;
      }
    }

    &--single{
      span{
        padding-left: 1rem;
        border-left: .1rem solid rgba($gray, .3);
      }

      i{
        content: "\f107";
        cursor: pointer;
        display: inline-block;
        @include font($font:FontAwesome, $size:2.6rem, $line_height:2.6rem, $color:$gray, $font_weight:bold);
        text-rendering: auto;
        padding: 1rem 1rem 1.5rem 0;
        margin-right: -.5rem;
        position: relative;
        top: .2rem;
      }
    }

  }
}