.autocomplete{
    &-suggestions {
        text-align: left;
        cursor: default;
        border: 1px solid #ccc;
        border-top: 0;
        background: #fff;

        /* core styles should not be changed */
        position: absolute;
        display: none;
        z-index: 9999;
        max-height: 254px;
        overflow: hidden;
        overflow-y: auto;
        box-sizing: border-box;
    }

    &-suggestion {
        @include font($font:$font, $size:1.4rem, $line_height:2rem, $color:$gray, $font_weight:$regular);
        position: relative;
        padding: .6rem 1rem .6em 1rem;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;

        b {
            font-weight: normal;
            color: $color-primary;
        }

        &.selected {
            background: rgba($gray, .1);
        }
    }
}
