.product{
  width: 100%;
  border: solid rgba($gray, .3) .1rem;
  position: relative;
  background-color: $white;
  display: block;
  cursor: pointer;
  transition: box-shadow .5s $ease-out-cubic;

  > a{
   position: absolute;
   top: 0;
   left: 0;
   width: 100%;
   height: 100%;
   z-index: 10;
  }

  &:hover{
    box-shadow: 0 14px 28px rgba(0,0,0,0.1), 0 10px 10px rgba(0,0,0,0.1);

    .product_picture img{
      transform: scale(1.2);
    }

    .product_button{
      background: $color-second;
      color: $white;
    }
  }

  &:after{
    content: "";
    display: block;
    padding-bottom: 100%;
  }

  &_wrapper{
    background-color: $background-gray;
    padding: 4rem 0;
    min-height: 50rem;
  }

  &_content{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }

  &_title{
    text-transform: uppercase;
    display: flex;
    min-height: 15%;
    align-items: center;
    @include font($font:$font, $size:1.8rem, $line_height:2rem, $color:$gray, $font_weight:$bold);
    width: calc(85% - 3rem);
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
    margin-left: 1.5rem;
    border-bottom: solid rgba($gray, .3) .1rem;
  }

  &_picture{
    height: 36%;
    margin: 3rem 0 0 0;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;

    img{
      height: 100%;
      width: auto;
      transform-origin: center center;
      transition: all .5s $ease-out-cubic;
    }
  }

  &_category{
    position: absolute;
    width: 15%;
    top: 0;
    right: 0;

    li{
      width: 100%;
      border-left: solid rgba($gray, .3) .1rem;
      border-top: solid rgba($gray, .3) .1rem;
      border-bottom: solid rgba($gray, .3) .1rem;
      background-size: 65% 65%;
      background-repeat: no-repeat;
      background-position: center;

      &:first-child{
        border-top: 0;
      }

      &:last-child{
        border-top: 0;
      }

      &:after{
        content: "";
        display: block;
        padding-bottom: 100%;
      }
    }
  }

  &_application{
    padding: .5rem 1.5rem 1.5rem 1.5rem;

    @include mq($from: sm) {
      padding: 1.5rem;
    }

    strong{
      @include font($font:$font, $size:1.6rem, $line_height:1.6rem, $color:$gray, $font_weight:$bold);
      display: inline-block;
      margin-bottom: 1rem;
    }

    &_tags{
      li{
        @include font($font:$font, $size:1.1rem, $line_height:1.4rem, $color:$white, $font_weight:$regular);
        background-color: $gray-product;
        display: inline-block;
        padding: .5rem 1rem;
        margin-right: .5rem;
        margin-bottom: .5rem;
        border-radius: .5rem;

        @include mq($from: md) {
          font-size: 1.3rem;
        }
      }
    }
  }

  &_button{
    background-color: $gray-product;
    @include font($font:$font, $size:1.4rem, $line_height:1.4rem, $color:$white, $font_weight:$bold);
    padding: 1.5rem 2rem;
    border: solid rgba($gray, .3) .1rem;
    text-transform: uppercase;
    position: absolute;
    right: -.1rem;
    bottom: 0;
    transform: translateY(50%);
    transition: all .5s;

    &:focus{
      color: $white;
      text-decoration: none;
    }
  }
}