.category{
    &__headband{
        li{
            @include font($font:$font, $size:1.4rem, $line_height:2.5rem, $color:$gray, $font_weight:$regular);
            text-transform: uppercase;
            display: inline-block;
            margin-right: 1.5rem;

            &:first-child{
                margin-right: 2rem;
                font-weight: $bold;
                color: $color-primary;
            }
        }

        i{
            margin-right: .5rem;
            cursor: pointer;
            transition: all 1s;

            &:hover{
                color: $color-primary;
            }
        }
    }

    &__button{
        background-color: $color-primary;
    }
}