.page-simple{
    padding: 6rem 0;

    &_title{
        @include font($font:$font, $size:6rem, $line_height:null, $color:$color-primary, $font_weight:$bold);
        margin-bottom: 4rem;
        display: inline-block;
        letter-spacing: .2rem;
    }

    &_content{
        @include font($font:$font, $size:1.5rem, $line_height:2.5rem, $color:$gray, $font_weight:$regular);

        h1{
            @include font($font:$font, $size:2rem, $line_height:3.5rem, $color:$color-second, $font_weight:$bold);
            margin-bottom: 4rem;

        }

        p{
            margin-bottom: 2rem;
        }

        strong, b{
            margin-top: 2rem;
            font-size: 2rem;
            color: $color-primary;
        }

        blockquote{
            &:before{
                content: "";
                display: block;
                height: .2rem;
                width: 8%;
                background: $color-second;
                margin-bottom: 1.5rem;
                margin-top: 5rem;
            }

            p{
                @include font($font:$font-title, $size:2.6rem, $line_height:2.5rem, $color:$color-primary, $font_weight:$regular);
            }
        }
    }
}